import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'
import Events from './views/Eventos.vue'
import chart1 from './views/NewChart.vue'
import c_anual from './components/comensales'
import costoPromedio from './components/CostoPromedio/costoPromedio'

Vue.use(Router)

export default new Router({
  mode: 'history',

  routes: [
    // loads Home component
    {
      path: '/reportes/',
      name: 'home',
      component: Home
    },
    {
      path: '/reportes/eventos/:evento',
      name: 'evento',
      component: Events
    },
    {
      path: '/reportes/chart1',
      name: 'chart1',
      component: chart1
    },
    {
      path: '/reportes/eventos/',
      name: 'charts Events',
      component: Events
    },
    {
      path: '/reportes/comensales/anual',
      name: 'Comensales',
      component: c_anual
    },
    {
      path: '/reportes/costoPromedio',
      name: 'Costo Promedio',
      component: costoPromedio
    },
    {
      path: '/page/:sectionSlug',
      name: 'dynamicContent',
      // route level code-splitting
      // this generates a separate chunk (dynamicContent.[hash].js) for this route
      // which is lazy-loaded when the route is visited.
      component: () => import(/* webpackChunkName: "dynamicContent" */ './views/DynamicContent.vue')
    },

  ]
})
